@font-face {
  font-family: "Poppins";
  font-weight: 200;
  font-style: normal;
  font-display: swap;
  src: local("Poppins ExtraLight"), url("../fonts/Poppins-ExtraLight.woff2") format("woff2"), url("../fonts/Poppins-ExtraLight.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Poppins Regular"), url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins-Regular.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Poppins Medium"), url("../fonts/Poppins-Medium.woff") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Poppins Bold"), url("../fonts/Poppins-Bold.woff2") format("woff2"), url("../fonts/Poppins-Bold.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
}
img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.list {
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  margin: 0 auto;
  min-width: 1200px;
  color: #000000;
  display: flex;
  gap: 50px;
  justify-content: center;
  align-items: center;
  padding: 50px 100px;
}
.list__item {
  width: 240px;
  padding: 25px 20px;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  background-color: #FFFFFF;
  color: #8d8d8d;
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .list__item {
    height: 250px;
    margin-right: 20px;
  }
}
.list__item--background-green {
  background-color: #41E398;
  color: #FFFFFF;
}
.list__wrap {
  display: flex;
  justify-content: space-between;
}
.list__image-holder--background {
  height: 65px;
  width: 65px;
  border-radius: 50%;
  background-color: #2EAE77;
}
.list__image-holder--background:hover {
  background-color: rgba(255, 255, 255, 0.7);
}
.list__image-holder--background:hover svg path {
  fill: #58E5Ef;
}
.list__image-holder svg {
  position: relative;
  top: 16px;
  left: 18px;
  transition: all 0.2s ease-in-out;
}
.list__image-holder svg:hover {
  transform: rotate(360deg) scale(1);
}
.list__title {
  font-size: 36px;
  font-weight: thin;
  margin: 10px 0;
}
.list__item:nth-child(2) .list__image-holder:hover svg path {
  fill: #41E398;
}
.holder__title {
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 24px;
  line-height: 22px;
  text-align: right;
  margin-bottom: 15px;
}
.holder__list {
  list-style: none;
  padding: 0;
  text-align: right;
  text-transform: capitalize;
  font-size: 16px;
  margin-top: 0;
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .holder__list {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
}
.holder__label {
  display: flex;
  justify-content: right;
}
.holder__checkmark {
  height: 13px;
  width: 13px;
  border: 1.5px solid #FFFFFF;
  border-radius: 50%;
  position: relative;
  margin-top: 5px;
  margin-left: 15px;
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .holder__checkmark {
    border: 1px solid #FFFFFF;
  }
}
.holder__input--custome {
  position: relative;
}
.holder__input--custome:checked ~ .holder__checkmark::before {
  content: "";
  height: 8px;
  width: 5px;
  background-color: #41E398;
  position: absolute;
  right: 0;
  transform: rotate(45deg);
}
.holder__input--custome:checked ~ .holder__checkmark::after {
  content: "";
  height: 3px;
  width: 11px;
  border-bottom: 1.5px solid #FFFFFF;
  border-left: 1.5px solid #FFFFFF;
  position: absolute;
  transform: rotate(-45deg);
  left: 2px;
  top: 0;
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .holder__input--custome:checked ~ .holder__checkmark::after {
    border-bottom: 1px solid #FFFFFF;
    border-left: 1px solid #FFFFFF;
    top: 1px;
    left: 3px;
  }
}
.holder__input--custome:hover ~ .holder__checkmark::after {
  border-bottom: 1.5px solid #2EAE77;
  border-left: 1.5px solid #2EAE77;
}
.holder__input--custome:focus ~ .holder__checkmark {
  outline: 1.8px solid #8d8d8d;
}
.holder__span {
  font-size: 16px;
  font-weight: 200;
}
.holder__input--checkbox-grey ~ .holder__checkmark {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1.5px solid #8d8d8d;
  text-align: right;
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .holder__input--checkbox-grey ~ .holder__checkmark {
    border: 1px solid #8d8d8d;
  }
}
.holder__input--checkbox-grey:checked ~ .holder__checkmark {
  border: 1.5px solid #58E5Ef;
  background-color: rgba(88, 229, 239, 0.3);
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .holder__input--checkbox-grey:checked ~ .holder__checkmark {
    border: 1px solid #58E5Ef;
  }
}
.holder__input--checkbox-grey:hover ~ .holder__checkmark {
  border: 1.5px solid #58E5Ef;
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .holder__input--checkbox-grey:hover ~ .holder__checkmark {
    border: 1px solid #58E5Ef;
  }
}
.holder__input--checkbox-grey:focus ~ .holder__checkmark {
  border: 1.5px solid #41E398;
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .holder__input--checkbox-grey:focus ~ .holder__checkmark {
    border: 1px solid #41E398;
  }
}
.holder__list--margin {
  margin-bottom: 40px;
}
.holder__text {
  font-weight: 100;
  text-align: right;
  margin: 0;
}
.holder__text span {
  text-transform: uppercase;
}
